import React from "react";

//Class component VideoDetails............
    const VideoDetail = ({video}) => {

        if(!video){
            return <h2>loading....</h2>
        }
        const videoId = video.id.videoId;
        const url = `https://www.youtube.com/embed/${videoId}`;

        return(
            <div className="video-detail well col-md-8">
                <div className="embed-responsive embed-responsive-16by9 frame">
                    <iframe className="embed-responsive-item" title={video.snippet.title} src={url}></iframe>
                </div>
                <div className="details">
                    <div className="videoTitle">{video.snippet.title}</div>
                    <div className="videoDescription">{video.snippet.description}</div>
                </div>
            </div>
        );
    }

//VideoDetails bhej de....................
    export default VideoDetail;


