import React, {Component} from 'react';
//{Component} means Component =  React.Component which is a property of React class

    /*const SearchBar = () => {
        return(
            <div>
                <input placeholder="Enter your search..."/>
            </div>
        );
    }*/

class SearchBar extends Component {
    constructor(props) {
        super(props);

        this.state = {term : ''};
    }
    
    render() {
        return(
                <div className="input-group well">
                    <span className="input-group-addon">
                        <i className="glyphicon glyphicon-search"></i>
                    </span>
                    <input  type="text" 
                            className="form-control" 
                            placeholder="Search here" 
                            onChange={event => this.onInputChange(event.target.value)}
                            />
                </div>
        );
    }

        onInputChange(term){
            this.setState({term});
            this.props.onSearchTermChange(term);
        }

  /*  render() {
        return(
            <div>
                <input onChange={this.onInputChange} placeholder="Enter your search..."/>
            </div>
        );
    } */

    // onInputChange(event) {
    //     console.log(event.target.value);
    // }

    //NOTE : Instead of writing OnInputChange function we can make the input tag as
    //<input onChange={(enent) => console.log(event.target.value)}/>
}
export default SearchBar;


