//Import krle.........
    import React from 'react';
    import VideoListItem from './youtube_list_item';

//Functional component VideoList.......
    const VideoList = (props) => {
    const VideoItems = props.videos.map(
                                           (video) => {
                                                 return (
                                                            <VideoListItem   
                                                                onVideoSelect = {props.onVideoSelect}
                                                                key={video.etag} 
                                                                video={video}
                                                            />
                                                        );
                                                       }
                                        );
                                        
        return(
            <ul className="col-md-4 well list-group">
                {VideoItems}
            </ul>
        );
    }

//BHEJ DE MANGNE WALE KO..........
    export default VideoList;


