
import _ from 'lodash';
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import SearchBar from './component/search_bar';
import YTSearch from 'youtube-api-search';
import VideoList from './component/youtube_list';
import VideoDetail from './component/youtube_details';


//API KEY for YouTube
const API_KEY = 'AIzaSyBmGaWhHc43qyblmsNuBhHgIYifzgxda6U';


class App extends Component{
    
    constructor(props){
        super(props);
        this.state = {videos : [],
                      selectedVideo : null
                    };
    
        this.videoSearch('bass boosted');
    }

    videoSearch(term)
    {
        YTSearch({key:API_KEY, term: term}, (videos) => {
            this.setState({videos : videos,
                            selectedVideo : videos[0]
                        });
            // console.log(this.state.videos);
        });
    }


    render(){
        const videoSearch = _.debounce((term) => {this.videoSearch(term)}, 300);
        
        return(  
                <div>
                    <SearchBar onSearchTermChange={videoSearch}/>
                    <VideoDetail video = {this.state.selectedVideo}/>
                    <VideoList  onVideoSelect = {selectedVideo => this.setState({selectedVideo : selectedVideo})} 
                                videos={this.state.videos}
                                />
                </div>
                );
    }

}

ReactDOM.render(<App/>, document.querySelector('.container'));


